(function() {
  'use strict';

  angular
    .module('EVQueue', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ui.bootstrap','ui.router']);
})();

(function () {
    'use strict';

    angular
        .module('EVQueue')
        .factory('QueueService', QueueService);


    function QueueService($http, $log, $q,API) {

        return {
            getQueue: getQueue,
            joinQueue:joinQueue,
            getTestQueue:getTestQueue,
            removeQueue:removeQueue
        };

        function getQueue() {
            return $http.get(API + 'queue')
                .then(getQueueSuccess)
                ['catch'](getQueueFailed);

            function getQueueSuccess(response) {
                return response.data;
            }

            function getQueueFailed(error) {
                $log.error('XHR Failed for getQueue.' + error.data);
                return $q.reject(error);
            }
        }

        function getTestQueue() {
            return {
                unplugged:[
                    {"username":"unplugged","uuid":"21312"},
                    {"username":"unplugged","uuid":"12312"}
                ],
                plugged:[
                    {"username":"plugged1","uuid":"12312"},
                    {"username":"plugged2","uuid":"42142"}
                ]
            };
        }

        function joinQueue(user) {
            return $http.post(API+ 'queue',user)
                .then(joinQueueSuccess)
                ['catch'](joinQueueFailed);

            function joinQueueSuccess(response) {
                return response.data;
            }

            function joinQueueFailed(error) {
                $log.error('XHR Failed for getQueue.' + error.data);
                return $q.reject(error);
            }
        }

        function removeQueue(uid) {
            return $http.delete(API+ 'queue/'+uid)
                .then(removeQueueSuccess)
                ['catch'](removeQueueFailed);

            function removeQueueSuccess(response) {
                return response.data;
            }

            function removeQueueFailed(error) {
                $log.error('XHR Failed for getQueue.' + error.data);
                return $q.reject(error);
            }
        }
    }

})();

(function() {
    'use strict';

    angular
        .module('EVQueue')
        .controller('QueueController', MainController);

    /** @ngInject */
    function MainController($log,QueueService,$timeout) {
        $log.log("QueueController Init");
        var vm = this;
        vm.user = null;
        vm.isCollapsed = false;
        vm.queue =  {
            unplugged:[
                {"username":"unplugged","uuid":"21312"}
            ],
            plugged:[
                {"username":"plugged1","uuid":"12312"}
            ]
        };

        vm.getQueue = function(){
            QueueService.getQueue().then(function(data){
                vm.queue = data;
            });
        };

        vm.userIsQueued = false;

        vm.joinQueue = function(){
            vm.userIsQueued = true;
            QueueService.joinQueue({"username":vm.user}).then(function(data){
                vm.queue = data;
            });
        };

        vm.removeFromQueue = function(user){
            QueueService.removeQueue(user.uuid).then(function(data){
                vm.queue = data;
            });
        };

        $timeout(function(){
            vm.getQueue();
        },30000);

        vm.getQueue();
    }
})();

(function () {
    'use strict';

    angular
        .module('EVQueue')
        .service('LoginService', LoginService);


    function LoginService() {
        var user = null;
        this.logUser = function(username){
            user = username;
        };
        this.getUser = function(){
            return user;
        }
    }

})();

(function() {
    'use strict';

    angular
        .module('EVQueue')
        .controller('LoginController', LoginController);

    /** @ngInject */
    function LoginController($log,LoginService,$state) {
        var vm = this;
        vm.user = null;
        vm.logUser = function(){
            $log.log("user");
            LoginService.logUser(vm.user);
            $state.go("queue");
        };

    }
})();

(function() {
  'use strict';

  angular
    .module('EVQueue')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('EVQueue')
    .constant('AppName', 'EVQueue')
    .constant('API', '/api/');

})();

(function () {
    'use strict';

    angular
        .module('EVQueue')
        .config(config);

    /** @ngInject */
    function config($logProvider, $stateProvider,$urlRouterProvider) {
        // Enable log
        $logProvider.debugEnabled(true);

        $urlRouterProvider.otherwise('/');

        $stateProvider
            .state('login', {
                url: '/login',
                templateUrl: 'app/login/login.html',
                controller: 'LoginController',
                controllerAs: 'vm'
            })
            .state('queue', {
                url: '/',
                templateUrl: 'app/queue/queue.html',
                controller: 'QueueController',
                controllerAs: 'vm'
            });
    }

})();

angular.module("EVQueue").run(["$templateCache", function($templateCache) {$templateCache.put("app/login/login.html","<div class=\"row marketing\"><form><div class=\"form-group\"><label for=\"username\">Username</label> <input type=\"text\" ng-model=\"vm.user\" id=\"username\" placeholder=\"username\"></div><button type=\"button\" class=\"btn btn-primary\" ng-click=\"vm.logUser()\">Login</button></form></div>");
$templateCache.put("app/queue/queue.html","<div class=\"queue-view\"><div class=\"head-container\"><div class=\"container\"><div class=\"header\">EVQueue</div><div class=\"subhead\">Financial Engines</div><p>Simple utility app to keep track of the EV Cars queue. Go ahead and set your username and join the queue from your computer / phone whenever.</p><input placeholder=\"Username\" type=\"text\" class=\"form-control\" id=\"usr\" ng-model=\"vm.user\"> <button ng-disabled=\"userIsQueued\" type=\"button\" class=\"btn btn-head\" ng-click=\"vm.joinQueue()\">Join Queue</button><div class=\"row full-bat\"><i class=\"fa fa-battery-full\" ng-class=\"get\"></i></div></div></div><div class=\"row item\" ng-repeat=\"q in vm.queue.unplugged\" ng-click=\"vm.removeFromQueue(q)\"><div class=\"battery\"><i class=\"fa fa-battery-empty\"></i></div><div ng-bind=\"q.username\"></div><div class=\"close-btn\"><i class=\"fa fa-times-circle\"></i></div></div></div>");}]);